body {
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #002f6c;
    font-size: 1.55vh;
}

#root{
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: stretch;
}
div .game{
    max-width: 600px;
    padding: 1em;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: space-around;
}

div .game-grid{
    padding-top: 2em;
    padding-bottom: 2em;
    flex: 0 1 auto;
    display: flex;
    justify-content: space-around;
    background-color: #4f83cc;
}

div .game-grid *{
    flex-shrink: 0;
}

div .game-grid .left-info{
    flex-shrink: 0;
    flex-grow: 1;
    width: 6.5em;
}

div .game-grid .right-info{
    padding-bottom: 5em;
    flex-shrink: 0;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    align-items: center;

    color: #DDDDDD;
    width: 6.5em;
}

.controls{
    padding-top: 2em;
    padding-bottom: 2em;
    display: flex;
    flex: 0 1 auto;
    justify-content: center;
    background-color: #01579b;
}

div p {
    text-align: center;
    padding: 0;
    margin: 0.075em;
    height: 1.50em;
}

div b {
    border: 0.075em solid black;
    position: relative;
    width: 1.50em;
    height: 1.50em;
    margin-right: 0.075em;
    display: inline-block;
}

.white {
    background-color: aliceblue;
}
.green {
    background-color: #2ECC40;
    outline: 0.075em solid #DDDDDD;
    outline-offset: -3px;
}
